body {
  background-color: #282c34;
  color: white;
  font-family: Arial, sans-serif;
  transition: all 0.25s linear;
}

h1 {
  text-align: center;
  padding: 20px 0;
}

div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

input {
  margin-bottom: 10px;
  padding: 10px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  background-color: #444;
  color: white;
  transition: all 0.3s ease-in-out;
}

input:focus {
  box-shadow: 0 0 10px #61dafb;
  outline: none;
}

button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 1em;
  background-color: #61dafb;
  color: #282c34;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

button:hover {
  background-color: #282c34;
  color: #61dafb;
  border: 2px solid #61dafb;
}

h2 {
  color: #61dafb;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease-in-out;
}
